
    import {defineComponent} from 'vue';
    import {PlusOutlined} from '@ant-design/icons-vue';
    import dynamicTags from './js/index';
    export default defineComponent({
        components:{
            PlusOutlined
        },
        props:{
            maxLength: Number,
            inputMaxLenght: Number
        },
        emits: ["change"],
        setup(props,context){
            const {
                inputDom,
                tags,
                inputVisible,
                inputValue,
                itemMaxLenght,
                btnVisible,
                handleClose,
                showInput,
                handleInputConfirm,
            } = dynamicTags(props,context);

            return {
                inputDom,
                tags,
                inputVisible,
                inputValue,
                itemMaxLenght,
                btnVisible,
                handleClose,
                showInput,
                handleInputConfirm,
            }
        }
    })
